import React from "react";
import MovieList from "./MovieList";
import useFetch from "./useFetch";
const TvShows = () => {
  const { error, isPending, data: movies } = useFetch("/movies");
  return (
    <div className="home">
      {error && <div>{error}</div>}
      {isPending && <div>Loading...</div>}
      <h2>Featured Tv Shows</h2>
      {movies && (
        <MovieList
          className="movie-center"
          movies={movies.filter(
            (movie) => movie.Type === "series" && movie.Featured === "yes"
          )}
        />
      )}
      {error && <div>{error}</div>}
      {isPending && <div>Loading...</div>}
      <h2 className="second-title">All Tv Shows</h2>
      {movies && (
        <MovieList movies={movies.filter((movie) => movie.Type === "series")} />
      )}
    </div>
  );
};

export default TvShows;
